class Application extends React.Component {
  state = {
    collapsed: false,
    page: 1,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };
  render() {
    const { collapsed } = this.state;
    const styleObj = {
      fontSize: 36,
      color: "#b3240d",
      textAlign: "left",
      paddingTop: "30px",
      paddingLeft: "30px",
      "font-family": 'liana'
    }
    const styleObj2 = {
      fontSize: 20,
      color: "#b3240d",
      textAlign: "left",
      paddingTop: "30px",
      paddingLeft: "30px",
      "font-family": 'liana'
    }
    let menuOptionClicked = (data) => {
      console.log("!!" + data.key)
      this.setState({page:data.key});
    };
    return (
        <Layout style={{ minHeight: '100vh' }} theme="light">
          <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse} theme="light">
            <p style={styleObj}>Юлия Ризен</p>
            <MenuLeft handler={menuOptionClicked}/>
          </Sider>
          <Layout className="site-layout">
            {/*<Header className="site-layout-background" style={{ padding: 0 }} theme="light"/>*/}
            <MenuRight page={this.state.page}/>
            <Footer style={{ textAlign: 'center' }} theme="light">Uphoto ©2022 Created by PByH</Footer>
          </Layout>
        </Layout>
    )
  }
}

